//文字コードはutf-8
@charset "UTF-8";

@use "../global" as g;

/* reset css
==============================================================*/
body,
h1, h2, h3, h4, h5, h6,
p, ol, ul, dl, dt, dd, form,
figure{
  margin:0;
  padding:0;
}

html{
  overflow-x: hidden;
  scroll-behavior: smooth;
}

h1, h2, h3, h4, h5, h6{font-size:100%; font-weight:normal;}

strong{font-weight:normal;}

table, td, th, tr, thead, tbody{border:none;}
table th{ text-align:left; font-weight:normal;}
table{ border-collapse:collapse;}

ul, ol{list-style:none;}

a{text-decoration:none;}
a img{border:none;}

img{ vertical-align:bottom;}

textarea{
  font-size:1em;
  border: none;
  border-radius: 0;
  outline: none;
  -webkit-appearance: none;
  @include g.box_shadow(none);
}
select{
  font-size:1em;
  border: none;
  border-radius: 0;
  outline: none;
  -webkit-appearance: none;
  appearance: none;
  @include g.box_shadow(none);
  &::-ms-expand{
    display: none;
  }
}

header,footer,section,nav,main,
figure, figcaption, time, small{display:block;}

input{
  &[type="submit"],
  &[type="image"]{
    border:none;
    padding: 0;
  }
  &[type="text"],
  &[type="email"],
  &[type="tel"],
  &[type="number"],
  &[type="password"]{
    outline: none;
    appearance: none;
    -webkit-appearance: none;
    border-radius: 0;
    @include g.box_shadow(none);
  }
  &[type="number"]{
    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button{
      appearance: none;
      -webkit-appearance: none;
      -moz-appearance: none;
      margin: 0;
    }
    -moz-appearance: textfield;
  }
}
button{
  border:none;
  margin:0;
  cursor: pointer;
}
input{
  &[type="submit"],
  &[type="image"],
  &[type="reset"]{
    cursor: pointer;
  }
}

address{
  font-style:normal;
}

input,
button,
select,
textarea{
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  outline: none;
}
input::placeholder,
textarea::placeholder{
  color: #999;
}